import { useEffect, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import styles from './Search.module.css';
import searchIcon from '../../assets/pic/search.svg';
import { searchStores } from '../../apis/storeSearchApi';

const Search = () => {
    const [searchParams, setSearchParams] = useSearchParams();
    
    // URL   keyword  (  )
    const keywordParam = searchParams.get('keyword') || '';

    // UI   
    const [keyword, setKeyword] = useState(keywordParam);
    
    const [results, setResults] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');

    // URL  (: ) (input)  
    useEffect(() => {
        setKeyword(keywordParam);
    }, [keywordParam]);

    //    (URL    )
    useEffect(() => {
        const term = keywordParam.trim();
        
        //    
        if (!term) {
            setResults([]);
            setError('');
            return;
        }

        let isCanceled = false;
        setLoading(true);
        setError('');
        
        // API 
        searchStores(term)
            .then((data) => {
                if (!isCanceled) {
                    setResults(Array.isArray(data) ? data : []);
                }
            })
            .catch((err) => {
                if (!isCanceled) {
                    setError(err?.message || '   .');
                }
            })
            .finally(() => {
                if (!isCanceled) setLoading(false);
            });

        return () => {
            isCanceled = true;
        };
    }, [keywordParam]);

    const handleSubmit = (event) => {
        event.preventDefault();
        const term = keyword.trim();
        if (!term) return;
        
        //    URL  ->  useEffect 
        setSearchParams({ keyword: term });
    };

    const hasQuery = keywordParam.trim().length > 0;
    const statusMessage = !hasQuery
        ? '   .'
        : loading
        ? ' ...'
        : error
        ? error
        : results.length === 0
        ? '  .'
        : '';

    return (
        <div className={styles.page}>
            <form className={styles.searchBar} onSubmit={handleSubmit}>
                <button type="submit" className={styles.searchButton} aria-label="">
                    <img src={searchIcon} alt="" className={styles.searchIcon} />
                </button>
                <input
                    type="search"
                    className={styles.searchInput}
                    placeholder=" "
                    value={keyword}
                    onChange={(event) => setKeyword(event.target.value)}
                />
            </form>

            {statusMessage && (
                <p className={`${styles.helperText} ${error ? styles.errorText : ''}`}>{statusMessage}</p>
            )}

            {!error && !loading && results.length > 0 && (
                <div className={styles.results}>
                    {results.map((store, index) => {
                        const key = store.link || `${store.storeName}-${store.address}-${index}`;
                        
                        const CardContent = (
                            <>
                                <div className={styles.thumbnail}>
                                    {store.imageUrl ? (
                                        <img
                                            src={store.imageUrl}
                                            alt={`${store.storeName} `}
                                            className={styles.thumbnailImage}
                                        />
                                    ) : (
                                        <div className={styles.thumbnailPlaceholder}>  </div>
                                    )}
                                </div>
                                <div className={styles.cardBody}>
                                    <div className={styles.cardHeader}>
                                        <h3 className={styles.storeName}>{store.storeName}</h3>
                                        {store.rating && (
                                            <span className={styles.ratingBadge}> {store.rating}</span>
                                        )}
                                    </div>
                                    {store.category && <p className={styles.category}>{store.category}</p>}
                                    {store.address && <p className={styles.address}>{store.address}</p>}
                                    {store.reviewCount && (
                                        <p className={styles.reviewCount}>{store.reviewCount} </p>
                                    )}
                                </div>
                            </>
                        );

                        return store.link ? (
                            <a
                                key={key}
                                className={styles.resultCard}
                                href={store.link}
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                {CardContent}
                            </a>
                        ) : (
                            <div key={key} className={styles.resultCard} role="article">
                                {CardContent}
                            </div>
                        );
                    })}
                </div>
            )}
        </div>
    );
};

export default Search;